*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  width: 100%;
}

h1, h2, h3 {
  line-height: 1;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  position: relative;
  line-height: 1.5;
  font-family: "Rubik", sans-serif;
  background: var(--vdark-blue);
}

.container {
  width: min(90%, 75rem);
  margin-inline: auto;
}

:root {
  --white: #fff;
  --black: #000;
  --prim-blue: hsl(246, 80%, 60%);
  --light-orange: hsl(15, 100%, 70%);
  --soft-blue: hsl(195, 74%, 62%);
  --light-red: hsl(348, 100%, 68%);
  --lime-green: hsl(145, 58%, 55%);
  --prim-violet: hsl(264, 64%, 52%);
  --soft-yellow: hsl(43, 84%, 65%);
  --vdark-blue: hsl(226, 43%, 10%);
  --dark-blue: hsl(235, 46%, 20%);
  --dark-blue-hover: hsla(236, 41%, 34%, 1);
  --desat-blue: hsl(235, 45%, 61%);
  --pale-blue: hsl(236, 100%, 87%);
  --ff-rubik: "Rubik", sans-serif;
}

.main {
  display: grid;
  gap: 1.25rem;
  padding-block: 5rem;
}
.main .perfil {
  border-radius: 0.9375rem;
  background: var(--dark-blue);
}
.main .perfil__user {
  background: var(--prim-blue);
  display: flex;
  gap: 1rem;
  align-items: center;
  border-radius: 0.9375rem;
  padding: 1.875rem;
}
.main .perfil__user .user__img {
  width: 70px;
  height: 70px;
  display: grid;
  place-items: center;
}
.main .perfil__user .user__img img {
  border: 2px solid var(--white);
  border-radius: 50%;
}
@media (min-width: 37.5em) {
  .main .perfil__user .user__img {
    width: 84px;
    height: 84px;
  }
}
.main .perfil__user .user__info {
  color: var(--pale-blue);
}
.main .perfil__user .user__info h1 {
  font-size: 1.5rem;
  font-weight: 300;
  color: var(--white);
}
@media (min-width: 60em) {
  .main .perfil__user .user__info h1 {
    font-size: 2.5rem;
    line-height: 47px;
  }
}
@media (min-width: 60em) {
  .main .perfil__user .user__info {
    margin-top: 1rem;
  }
}
@media (min-width: 60em) {
  .main .perfil__user {
    flex-direction: column;
    align-items: flex-start;
    height: 70%;
  }
}
.main .perfil__menu {
  padding: 1.25rem;
  display: flex;
  font-size: 1.0625rem;
  justify-content: space-between;
  color: var(--desat-blue);
}
.main .perfil__menu li {
  max-width: max-content;
  cursor: pointer;
}
.main .perfil__menu li:hover {
  color: var(--white);
}
@media (min-width: 60em) {
  .main .perfil__menu {
    height: 30%;
    flex-direction: column;
  }
}
@media (min-width: 37.5em) {
  .main .perfil {
    grid-column: 1/-1;
  }
}
@media (min-width: 60em) {
  .main .perfil {
    grid-column: unset;
    grid-row: 1/span 2;
    height: 100%;
  }
}
.main .card {
  width: 100%;
  border-radius: 0.9375rem;
  background-position: 92% -1rem;
}
.main .card__info {
  margin-top: 2.5rem;
  background-color: var(--dark-blue);
  padding: 1.25rem;
  border-radius: 0.9375rem;
  display: grid;
  gap: 0.5rem;
}
.main .card__info:hover {
  background: var(--dark-blue-hover);
}
.main .card__info .info__title {
  display: flex;
  gap: 1.25rem;
  justify-content: space-between;
  align-items: center;
}
.main .card__info .info__title h4 {
  font-size: 1.0625rem;
  color: var(--white);
  font-weight: 500;
}
.main .card__info .info__title .dots {
  cursor: pointer;
}
.main .card__info .info__dados {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.25rem;
}
.main .card__info .info__dados .time {
  font-size: 2.0625rem;
  font-weight: 300;
  color: var(--white);
  line-height: 1;
}
@media (min-width: 60em) {
  .main .card__info .info__dados .time {
    font-size: 3.4375rem;
  }
}
.main .card__info .info__dados p {
  color: var(--pale-blue);
}
@media (min-width: 60em) {
  .main .card__info .info__dados {
    flex-direction: column;
    align-items: flex-start;
  }
}
.main .work {
  background-image: url(../../../img/icon-work.svg);
  background-repeat: no-repeat;
  background-color: var(--light-orange);
}
@media (min-width: 37.5em) {
  .main {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 60em) {
  .main {
    grid-template-columns: repeat(4, minmax(255px, 1fr));
  }
}