@use '../util' as *;

.main {
  display: grid;
  gap: rem(20);
  padding-block: rem(80);

  .perfil {
    border-radius: rem(15);
    background: var(--dark-blue);

    &__user {
      background: var(--prim-blue);
      display: flex;
      gap: rem(16);
      align-items: center;
      border-radius: rem(15);
      padding: rem(30);

      .user__img {
        width: 70px;
        height: 70px;
        display: grid;
        place-items: center;
        
        img {
          border: 2px solid var(--white);
          border-radius: 50%;
        }

        @include breakpoint(medium) {
          width: 84px;
          height: 84px;
        }
      }

      .user__info {
        color: var(--pale-blue);

        h1 {
          font-size: rem(24);
          font-weight: 300;
          color: var(--white);

          @include breakpoint(large) {
            font-size: rem(40);
            line-height: 47px;
          }
        }

        @include breakpoint(large) {
          margin-top: rem(16);
        }
      }

      @include breakpoint(large) {
        flex-direction: column;
        align-items: flex-start;
        height: 70%;
      }
    }

    &__menu {
      padding: rem(20);
      display: flex;
      font-size: rem(17);
      justify-content: space-between;
      color: var(--desat-blue);

      li {
        max-width: max-content;
        cursor: pointer;

        &:hover {
          color: var(--white);
        }
      }
      
      @include breakpoint(large) {
        height: 30%;
        flex-direction: column;
      }
    }

    @include breakpoint(medium) {
      grid-column: 1 / -1;
    }

    @include breakpoint(large) {
      grid-column: unset;
      grid-row: 1 / span 2;
      height: 100%;
    }
  }

  .card {
    width: 100%;
    border-radius: rem(15);
    background-position: 92% -1rem;;

    &__info {
      margin-top: rem(40);
      background-color: var(--dark-blue);
      padding: rem(20);
      border-radius: rem(15);
      display: grid;
      gap: rem(8);

     &:hover {
       background: var(--dark-blue-hover);
     }

     .info__title {
       display: flex;
       gap: rem(20);
       justify-content: space-between;
       align-items: center;
       
       h4 {
         font-size: rem(17);
         color: var(--white);
         font-weight: 500;
       }

       .dots {
         cursor: pointer;
       }
     }

     .info__dados {
       display: flex;
       justify-content: space-between;
       align-items: center;
       gap: rem(20);

       .time {
         font-size: rem(33);
         font-weight: 300;
         color: var(--white);
         line-height: 1;

         @include breakpoint(large) {
           font-size: rem(55);
         }
       }

       p {
         color: var(--pale-blue);
       }

       @include breakpoint(large) {
         flex-direction: column;
         align-items: flex-start;
       }
     }
    }
  }

  .work {
    background-image: url(../../../img/icon-work.svg);
    background-repeat: no-repeat;
    background-color: var(--light-orange);
  }

  @include breakpoint(medium) {
    grid-template-columns: repeat(2, 1fr);
  }

  @include breakpoint(large) {
    grid-template-columns: repeat(4, minmax(255px, 1fr));
  }
}